body {
  margin: 0;
}

.stars {
  display: flex;
  justify-content: center;
  align-items: center;
}

.strars__star {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
  background: url(./images/star.svg) left no-repeat;
}

.strars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--0 :nth-child(0) {
  background: url(./images/star-active.svg) left no-repeat;
}

.stars--1 :nth-child(1) {
  background: url(./images/star-active.svg) left no-repeat;
}

.stars--2 :nth-child(-n+2) {
  background: url(./images/star-active.svg) left no-repeat;
}

.stars--3 :nth-child(-n+3) {
  background: url(./images/star-active.svg) left no-repeat;
}

.stars--4 :nth-child(-n+4) {
  background: url(./images/star-active.svg) left no-repeat;
}

.stars--5 :nth-child(-n+5) {
  background: url(./images/star-active.svg) left no-repeat;
}
